import { Component, OnDestroy, OnInit } from '@angular/core';
import { skip, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

import { FooterPagesService } from '@services/modules/footer-pages/dataservice/footer-pages.service';
import { FooterPagesContent } from '@schemas/modules/footer-pages/footer-pages.model';
import { SettingsService } from '@services/services/settings-service';
import { ActivatedRoute } from '@angular/router';
import { ScrollService } from '@services/modules/core/scroll-service';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-help',
    templateUrl: './help.component.html',
    styleUrls: ['./help.component.scss'],
})
export class HelpComponent implements OnInit, OnDestroy {
    public content: FooterPagesContent | undefined;
    public signUpSuccessfully = false;
    public formProcessing = false;
    public openFaqContentId = '';

    private destroy$: Subject<boolean> = new Subject<boolean>();
    private scrollId = '';

    constructor(
        private footerPagesService: FooterPagesService,
        private settingsService: SettingsService,
        private activeRoute: ActivatedRoute,
        private scrollService: ScrollService,
        public platformService: PlatformService
    ) {
        this.scrollId = this.activeRoute.snapshot.queryParams['scrollTo'];
        if (this.scrollId && this.scrollId === 'Flights and trains') {
            this.openFaqContentId = 'How do I check-in to my flight?';
        }
    }

    ngOnInit() {
        this.getContent(true);

        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.getContent();
        });
    }

    public getContent(onInit = false): void {
        this.footerPagesService.getPageContent('help').subscribe((content) => {
            this.content = content;

            if (onInit && this.scrollId && this.scrollId !== '') {
                setTimeout(() => {
                    this.scrollService.scrollToElement(this.scrollId);
                }, 500);
            }
        });
    }

    public onFormSubmit(form: any): void {
        this.formProcessing = true;
        this.footerPagesService.sendContactForm(form).subscribe(
            () => {
                this.signUpSuccessfully = true;
                this.formProcessing = false;
            },
            () => {
                this.formProcessing = false;
            }
        );
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }
}
